@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?pf49wj');
  src:  url('../fonts/icomoon.eot?pf49wj#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?pf49wj') format('truetype'),
    url('../fonts/icomoon.woff?pf49wj') format('woff'),
    url('../fonts/icomoon.svg?pf49wj#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-home:before {
  content: "\e900";
}
.icon-compass:before {
  content: "\e949";
}
.icon-undo2:before {
  content: "\e967";
}
.icon-bubbles3:before {
  content: "\e96f";
}
.icon-user:before {
  content: "\e971";
}



